<template>
  <article>
      <h1>Lav ny post</h1>
      <form id="createForm">
          <input type="text" v-model="post.title" placeholder="Titel på post" required>
          <textarea rows="5" v-model="post.description" placeholder="beskrivelse af post"/>
          <input type="file" ref="fileInput" accept="image/*" @change="previewImage">
          <div>
              <img :src="post.img" class="image-preview">
          </div>
          <button type="button" @click="createPost">Lav post</button>
      </form>
  </article>
</template>

<script>
import { postRef } from '../firebase-db'
export default {
    data(){
        return{
            post:{
                title: "",
                description: "",
                img: null,
            }
        }
    },
    methods: {
        previewImage(){
            const imageFile = this.$refs.fileInput.files[0]
            const fileReader = new FileReader()
            fileReader.onload = (event) => {
                this.post.img = event.target.result
            }
            fileReader.readAsDataURL(imageFile)
        },
        createPost(){
            postRef.add(this.post)
            this.$router.push('/')
        }
    }
}
</script>

<style>
    #createForm{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 15%;
        margin: auto;
        height: 30vh;
    }

    #createForm img{
        width: 100%;
    }
</style>